import React, { useState } from 'react';
import _isEmpty from 'lodash.isempty';
import {
	Grid,
	Stack,
	Button,
	CircularProgress,
	makeStyles,
	Typography,
} from '@material-ui/core';
import { Formik } from 'formik';
import { gql, useMutation } from '@apollo/client';
import _get from 'lodash.get';
import { generatePath, useHistory } from 'react-router';
import {
	TJurisdictionalCost,
	TJurisdictionalCostItem,
	TSellRequest,
} from '../../../../../types';
import SettlementTermsTable from '../SettlementTerms/Table';
import { gridSpacing } from '../../../../../store/constant';
import AnimateButton from '../../../../../ui-component/extended/AnimateButton';
import graphqlSchema from '../../../../../graphql';
import { useSnackBar } from '../../../../../hooks';
import {
	convertBooleanToYesNo,
	prepareInitValue,
	prepareValues,
} from '../SettlementTerms/helper';
import { PATHS } from '../../../../../routes/path';
import { TABS } from '../../../../pages/property/EditPage';
import Hoa from '../SettlementTerms/Hoa';
import { validationAddHoa } from '../SettlementTerms/Validation';

type Props = {
	handleBack: () => void;
	formData: TSellRequest;
	refetch: any;
};

const useStyles = makeStyles({
	button: {
		marginRight: '10px',
	},
});

const AddSettlementTerms = ({ formData, handleBack, refetch }: Props) => {
	const classes = useStyles();
	const showSnackBar = useSnackBar();
	const history = useHistory();
	const jurisdictionalCost = _get(
		formData,
		'JurisdictionalCost',
		[]
	) as TJurisdictionalCost;
	const jurisdictionalCostItems = _get(
		jurisdictionalCost,
		'Items',
		[]
	) as Array<TJurisdictionalCostItem>;
	const { PropertyId: propertyId } = formData;
	const [isApproving, setIsApproving] = useState<boolean>(false);
	const initItems = prepareInitValue(jurisdictionalCostItems);
	const hoaDetectedOverride = _get(jurisdictionalCost, 'HOADetectedOverride');
	const hoaDetected = _get(jurisdictionalCost, 'HOADetected');
	const initialValues = {
		...initItems,
		hoaDetectedOverride: convertBooleanToYesNo(
			hoaDetectedOverride !== null ? hoaDetectedOverride : hoaDetected
		),
	};
	const [updatePropertyJurisdictionalCostPayors, { loading: isSaving }] =
		useMutation(gql(graphqlSchema.updatePropertyJurisdictionalCostPayors));
	const [approveAction] = useMutation(gql(graphqlSchema.approveProperty));

	const saveData = async (values: { [key: string]: string }) => {
		try {
			const variables = prepareValues(values, jurisdictionalCostItems, propertyId);
			await updatePropertyJurisdictionalCostPayors({
				variables,
			});
			showSnackBar({ message: 'Property updated', alertSeverity: 'success' });
			refetch();
		} catch (e) {
			const message = _get(e, 'message') || 'Something went wrong';
			showSnackBar({
				message,
			});
		}
	};

	const onSave = (values: { [key: string]: string }) => saveData(values);

	const onSubmit = async (values: { [key: string]: string }) => {
		try {
			setIsApproving(true);
			await saveData(values);
			await approveAction({
				variables: {
					PropertyId: propertyId,
				},
			});
			setIsApproving(false);
			const propertyLink = generatePath(PATHS.PROPERTY_EDIT, {
				id: propertyId,
				tab: TABS.actions,
			});
			history.push(propertyLink);
		} catch (e) {
			const message = _get(e, 'message') || 'Something went wrong';
			showSnackBar({
				message,
			});
			setIsApproving(false);
		}
	};
	const isDisabled = isApproving || isSaving;

	return (
		<Grid container spacing={gridSpacing}>
			<Grid item xs={12}>
				<Formik
					initialValues={initialValues}
					onSubmit={onSubmit}
					validationSchema={validationAddHoa}
				>
					{({ values, handleSubmit }) => (
						<form noValidate onSubmit={handleSubmit}>
							<Grid container spacing={3}>
								<Grid item xs={12} lg={12}>
									{_isEmpty(jurisdictionalCost) ? (
										<p>
											Customary Settlement Terms cannot be found, please contact site
											admin.
										</p>
									) : (
										<>
											<SettlementTermsTable jurisdictionalCost={jurisdictionalCost} />
											<Hoa jurisdictionalCost={jurisdictionalCost} />
										</>
									)}
								</Grid>
								<Grid item xs={12} lg={12}>
									<Typography variant='body2' sx={{ fontStyle: 'italic', pl: 2, color: 'error.main' }}>
										By pressing approve, the system will send an email notification to
										seller to begin onboarding. Agent will receive a CC copy.
									</Typography>
								</Grid>
								<Grid item xs={12}>
									<Stack direction='row' justifyContent='flex-end'>
										<AnimateButton>
											<Button onClick={handleBack} sx={{ my: 3, ml: 1 }}>
												Back
											</Button>
											<Button
												className={classes.button}
												color='primary'
												variant='outlined'
												type='button'
												disabled={isDisabled}
												sx={{ my: 3, ml: 1 }}
												onClick={() => onSave(values)}
											>
												{isSaving ? (
													<CircularProgress size='1.5rem' color='secondary' />
												) : (
													'Save'
												)}
											</Button>
											<Button
												color='primary'
												variant='contained'
												type='submit'
												disabled={isDisabled}
											>
												{isApproving ? (
													<CircularProgress size='1.5rem' color='secondary' />
												) : (
													'Approve'
												)}
											</Button>
										</AnimateButton>
									</Stack>
								</Grid>
							</Grid>
						</form>
					)}
				</Formik>
			</Grid>
		</Grid>
	);
};

export default AddSettlementTerms;
